import React from 'react';
import { Table } from 'reactstrap';
import { table } from './ParameterTable.module.css';

export default ({ children, withDefaultColumn = false }) => (
    <Table size='sm' borderless={true} className={table}>
        <thead>
            <tr>
                <th>Name</th>
                <th>Type</th>
                {withDefaultColumn && <th>Default</th>}
                <th>Description</th>
            </tr>
        </thead>
        <tbody>
            {children}
        </tbody>
    </Table>
);