import React from 'react';
import DefaultLayout from './DefaultLayout';
import { Link } from 'gatsby';
import styled from 'styled-components';

const NavLink = ({ to, children }) => (
    <Link to={to} className='nav-link text-white'>{children}</Link>
);

const SideMenu = styled.div`
    position: fixed;
    top: 56px;
    width: 240px;
    bottom: 0px;
    background-color: var(--dark);

    & > nav {
        color: white;
    }
`;

const Main = styled.div`
    margin-left: 240px;
    font-size: 14px;
`;

export default ({ children }) => (
    <DefaultLayout>
        <SideMenu className='p-1'>
            <nav className="nav flex-column">
                <NavLink to='/apidoc/layer/GeoserverLayer'>easyolmaps/layer/GeoserverLayer</NavLink>
                <NavLink to='/apidoc/vector/Drawing'>easyolmaps/vector/Drawing</NavLink>
                <NavLink to='/apidoc/style/Style'>easyolmaps/style/Style</NavLink>
            </nav>
        </SideMenu>
        <Main className='p-4'>
            {children}
        </Main>
    </DefaultLayout>
);